.NavBar .Label {
  display: inline-block;
}

.navbar {
  height: 60px;
  padding: 0;
  font-size: 0.875rem;
  background-color: white;
  box-shadow: 0 0.1px 0.1rem 0.1rem rgba(58, 59, 69, 0.15);
  user-select: none;
}

.navbar-main {
  z-index: 10000;
}

.navbar-brand {
  display: flex;
  height: 100%;
  margin: 0;
  padding: 14px 20px;
  background-color: gray;
  color: white !important;
  cursor: pointer;
}

.nav-icon {
  width: 32px;
  height: 32px;
  margin-right: 10px;
}

.navbar-toggler {
  margin-right: 10px;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
}

.navbar-toggler:hover {
  background-color: gray;
}

.navbar-collapse {
  background-color: white;
}

.nav-item:hover {
  background-color: lightgray;
}

.nav-link {
  width: 100%;
  height: 100%;
  padding: 0 20px !important;
  line-height: 60px;
  cursor: pointer;
}

.dropdown-toggle::after {
  display: none;
}

.nav-bar-area {
  height: 60px;
}