.DonatePage .downloadInProgress {
    margin: 50px 0;
}

.DonatePage .downloadInProgress .Label {
    margin: auto;
    font-size: 20pt;
}

.DonatePage .helpButton {
    position: fixed;
    bottom: 30px;
    right: 30px;
    margin: 30px auto;
    padding: 15px 35px;
    color: black;
    background-color: #ff6720;
    text-decoration: none;
}

.DonatePage .helpButton:hover {
    background-color: #ffb120;
}

.DonatePage .donateInfo {
    margin: 20px;
    font-size: 13pt;
}

.DonatePage .ButtonArea {
    display: block;
}

.DonatePage .ButtonArea .LinkButton {
    margin: 10px;
    padding: 25px;
    width: auto;
    font-size: 20pt;
}

.DonatePage .ButtonArea .LinkButton:hover {
    background-color: #ffb120;
}

.DonatePage .thankyou {
    margin: 50px auto;
    font-size: 13pt;
    color: gray;
}