.Modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99999999;
}

.Modal .ModalWindow {
    margin: 30px auto;
    width: 100%;
    max-width: 600px;
    max-height: 100%;
    background-color: white;
    border-radius: 10px;
    overflow: hidden;
}

.Modal .ModalBody {
    padding: 20px;
}

.Modal .ModalBody .ModalTitle {
    width: 100%;
    font-size: 22px;
}

.Modal .ModalBody .ModalContent {
    width: 100%;
    max-height: 300px;
    overflow-y: auto;
}

.Modal .ModalButtonArea {
    display: flex;
    background-color: #ff9100;
    justify-content: space-around;
}

.Modal .ModalButton {
    margin: 10px;
    width: calc(50% - 20px);
}