.HashTag {
    display: inline-block;
    margin: 2px;
    width: fit-content;
    padding: 0 5px;
    font-size: 17px;
    background-color: rgba(150, 150, 150, 0.5);
    border-radius: 5px;
    user-select: none;
    cursor: pointer;
}

.HashTag:hover {
    background-color: rgba(150, 150, 150, 0.8);
}