    text-decoration: none;
}

.HelpPage .installation .downloadButton:hover {
    background-color: #00b400;
}

.HelpPage .installation .downloadButton:active {
    background-color: #008600;
}

.HelpPage .imageToggler {
    position: fixed;
    display: flex;
    bottom: 30px;
    right: 30px;
    padding: 10px;
    background-color: rgba(145, 71, 255, 0.7);
    border-radius: 100px;
}
.ImageViewer {
    padding: 15px;
    max-width: 100%;
    max-height: 100%;
}
.Switch {
    margin: auto;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
}

.Switch .switch-checkbox {
    display: none;
}

.Switch .switch-label {
    display: block;
    position: relative;
    margin: unset;
    width: 50px;
    height: 26px;
    border-radius: 50px;
    background-color: #888888;
}

.Switch .label-switched {
    background-color: #00cf00;
}

.Switch .switch-ball {
    position: absolute;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    top: 2px;
    left: 2px;
    transition: transform 0.2s linear;
    background-color: #ffffff;
}

.Switch .ball-switched {
    transform: translateX(24px);
}
.DonatePage .downloadInProgress {
