.LinkButton {
	display: block;
    margin: 0 5px;
    padding: 7px 12px;
    width: fit-content;
    color: #000000;
    background-color: #eff0f2;
    border: none;
    border-radius: 5px;
    font-size: 15px;
    user-select: none;
}

.LinkButton:hover {
    background-color: #dadada;
}

.LinkButton:active {
	background-color: #b6b6b6;
}