.FooterArea {
  display: flex;
  height: 50px;
  padding: 15px;
  font-family: "Nanum Gothic", sans-serif !important;
  font-size: 12px;
  background-color: white;
  box-shadow: 0 0.1px 0.1rem 0.1rem rgba(58, 59, 69, 0.15);
}

.FooterArea .contact {
  display: flex;
  margin-left: auto;
}

@media (max-width: 600px) {
  .FooterArea .contact {
      display: none;
  }
}