.Switch {
    margin: auto;
    width: min-content;
    height: min-content;
}

.Switch .switch-checkbox {
    display: none;
}

.Switch .switch-label {
    display: block;
    position: relative;
    margin: unset;
    width: 50px;
    height: 26px;
    border-radius: 50px;
    background-color: #888888;
}

.Switch .label-switched {
    background-color: #00cf00;
}

.Switch .switch-ball {
    position: absolute;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    top: 2px;
    left: 2px;
    transition: transform 0.2s linear;
    background-color: #ffffff;
}

.Switch .ball-switched {
    transform: translateX(24px);
}