.Button {
	display: block;
    margin: 0 5px;
    padding: 7px 12px;
    height: fit-content;
    background-color: #eff0f2;
    border: none;
    border-radius: 5px;
    font-size: 15px;
	user-select: none;
	outline: none;
	cursor: pointer;
}

.Button:hover {
	background-color: #dadada;
}

.Button:active {
	background-color: #b6b6b6;
}